@import "../App.scss";

.movie {
  display: flex;
  flex-direction: column;
  &__image {
    display: flex;
    justify-content: center;
    overflow: hidden;
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.5),
      0 10px 10px -5px rgba(0, 0, 0, 0.7);
    &-img:hover {
      transform: scale(1.08);
      transition: transform linear 0.2s;
    }
  }
  &__info {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    a {
      text-decoration: none;
      color: inherit;
      height: 6.5rem;
    }
    &__title {
      font-size: 1.4rem;
      font-weight: bolder;
      line-height: 1.9rem;
      text-decoration: underline;
      &:hover {
        color: $yellow;
      }
    }
    &__genres,
    &__rating {
      font-size: 0.8rem;
      color: darken($lightGrey, 25%);
      margin-bottom: 10px;
    }
  }
}
